import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import ListGroup from 'react-bootstrap/ListGroup'
import {Button, message} from 'antd'
import ViewCode from './ViewCode'
import ReactDOM from 'react-dom'
import { Avatar } from 'antd';
import { UserOutlined } from '@ant-design/icons';
import axios from 'axios'
import Comment from './Comment'
import { useEffect, useState } from 'react'

function Params({product}){

    const [profile, setProfile] = useState('')

    const pay = () => {
        axios.post("/pay", {name: product.name, APIkey: product.APIkey, html: product.html, css: product.css, js: product.js, price: product.price}).then((response) => {
            window.location.href = response.data
        }).catch(error => message.error("Error, could not make payment, user has entered wrong API key"))
    }
    
    const runCode = () => {
        ReactDOM.render(<ViewCode html={product.html} css={product.css} js={product.js} name={product.name} />, document.getElementById('root'))
    }
    
    useEffect(() => {
        axios.get(`http://localhost:5000/get/Computer4062`).then((res) => {
            setProfile(res.data.profile).catch(error => message.error("Error while loading profile"))
        })
    }, [])

    useEffect(() => {
        const run = () => {
            const htmlCode = product.html;
            const cssCode = "<style>" + product.css + "</style>";
            const jsCode = product.js;
            const output = document.querySelector(".run #myiframe");
            
            output.contentDocument.body.innerHTML = htmlCode+cssCode;
            output.contentWindow.eval(jsCode)
        }
        run()
    }, [])

    return(
        <Row>
            <Col md={6}>
                <div class="cover-image">
                    <div className="run">
                        <iframe className='compiler frame' title={product.name} id="myiframe" style={{height: 400}} onload="injectJS()"></iframe>
                    </div>
                </div>
                <Comment name={product.name} />
            </Col>
            <Col md={3} className='details'>
                <ListGroup.Item>
                    <h1>{product.name}</h1>
                </ListGroup.Item>
                <hr />
                <ListGroup.Item>price: ${product.price}</ListGroup.Item>
                <hr/>
                <ListGroup.Item>Description: {product.description}</ListGroup.Item>
                <hr />
                <ListGroup.Item>Made By: <Avatar shape="square" src={profile} size={20} icon={<UserOutlined />} />{product.username}</ListGroup.Item>
                <hr />
                <Button className='primary1' style={{width: 450}} onClick={runCode}>Run Code On FullScreen</Button>
                <hr />
                <Button onClick={pay} style={{width: 450}}>Purchse Code</Button>
                <hr />
            </Col>
        </Row>
    )
}

export default Params