import { useState } from 'react';
import { Button, Form, Input, message, Modal, InputNumber} from 'antd';
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import axios from 'axios'
import { useNavigate } from 'react-router-dom';
import NavBar from '../Components/NavBar'

const Post = () => {
    const navigate = useNavigate()

    const [username, setUsername] = useState('')
    const [password, setPassword] = useState('')
    const [name, setName] = useState('')
    const [category, setCategory] = useState('')
    const [image, setImage] = useState('')
    const [price, setPrice] = useState('')
    const [email, setEmail] = useState('')
    const [APIkey, setAPIkey] = useState('')
    const [description, setDescription] = useState('')
    const [html, setHtml] = useState('')
    const [css, setCss] = useState('')
    const [js, setJs] = useState('')

    const finishButton = async() => {
            console.log(price)
            axios.get(`/acc/find/${username}/${password}`).then((res) => {
            if(res.data.length === 0){
                message.error("Error account not found")
            }
            else{
                message.success("Account found, posted")
                navigate("/")
                axios.post("/code/post", {username, name, email, category, image, html, css, js, description, APIkey, price}).then((res2) => {
                message.success("Posted")
                }).catch(error => message.error("Could not be posted "))
            }
            })
      }

    const submitCode = () => {
        const htmlCode = document.querySelector(".editor #html-code").value;
        const cssCode = "<style>" + document.querySelector(".editor #css-code").value + "</style>";
        const jsCode = document.querySelector(".editor #js-code").value;
        const output = document.querySelector(".editor #output");

        output.contentDocument.body.innerHTML = htmlCode+cssCode;
        output.contentWindow.eval(jsCode)
    }
    const [isModalOpen, setIsModalOpen] = useState(false)

    const showModal = () => {
        setIsModalOpen(true);
      };
    
      const handleOk = () => {
        setIsModalOpen(false);
      };
    
      const handleCancel = () => {
        setIsModalOpen(false);
      };

      const pay = () => {
        axios.post("/pay", {name: name, APIkey: APIkey, html: html, css: css, js: js, price: price}).then((response) => {
            window.location.href = response.data
        }).catch(error => message.error("Error, could not make payment, user has entered wrong API key"))
       }

    return (
    <NavBar>
    <Form className='posting-form'>
        <Row>
            <Col>
                <div className='btn-modal'>
                    <Button onClick={showModal} >Click and read before you post</Button>
                </div>
            </Col>
        </Row>
            <Modal title="Please Read" open={isModalOpen} onOk={handleOk} onCancel={handleCancel}>
                <p>Once you post you would not be able to edit any of the information you have added So please rember to enter the correct details</p>
                <p>It is also recomanded that you create an alternative stripe account detecated to codemarket and it is not recomanded to use your personal stripe account.</p>
                <p>Your codes must be sold at atleast a dolar for a payment to work</p>
            </Modal>
        <Row>
            <Form.Item label="Project name" >
            <Input onChange={(e) => setName(e.target.value)} />
            </Form.Item>
        </Row>
        <Row>
            <Col>
                <Form.Item label="Username">
                    <Input onChange={(e) => setUsername(e.target.value)} placeholder="Your username..." />
                </Form.Item>
            </Col>
            <Col>
                <Form.Item label="Password" >
                    <Input.Password onChange={(e) => setPassword(e.target.value)} placeholder='your password...' />
                </Form.Item>
            </Col>
            <Col>
                <Form.Item label="Category" >
                    <Input onChange={(e) => setCategory(e.target.value)} placeholder='category' />
                </Form.Item>
            </Col>
        </Row>
        <Row>
            <Col>
                <Form.Item label="Image" >
                    <Input onChange={(e) => setImage(e.target.value)} placeholder="Image URL" />
                </Form.Item>
            </Col>
            <Col>
                <Form.Item label="Price" >
                    <Input onChange={(e) => setPrice(e.target.value)} placeholder='price in usd' />
                </Form.Item>
            </Col>
            <Col>
                <Form.Item label="email" >
                    <Input onChange={(e) => setEmail(e.target.value)} placeholder='email' />
                </Form.Item>
            </Col>
        </Row>
        <Row>
            <Col>
                <Form.Item label="APIket" >
                    <Input onChange={(e) => setAPIkey(e.target.value)} placeholder="Your stripe secret key" />
                </Form.Item>
            </Col>
        </Row>
        <Row>
            <Col>
                <Form.Item label="Test API key" >
                    <p>If a stripe payment page popups it means that the key is valid</p>
                    <Button onClick={pay}>Test Stripe Secret Key</Button>
                </Form.Item>
            </Col>
        </Row>
        <Row>
            <Col>
                <Form.Item label="Description" >
                    <Input.TextArea label="description" onChange={(e) => setDescription(e.target.value)} placeholder='enter your description here' rows={5} cols={260} ></Input.TextArea>
                </Form.Item>
            </Col>
        </Row>
        <div class="editor">
                <Row>
              <Col>
                <h2>HTML code</h2>
                <hr />
                <br />
                <Input.TextArea placeholder='enter your html code here' onChange={(e) => setHtml(e.target.value)} id="html-code" rows={20} cols={70}></Input.TextArea>
              </Col>
              <Col>
                <h2>CSS code</h2>
                <hr />
                <br />
                <Input.TextArea placeholder='enter your css code here' onChange={(e) => setCss(e.target.value)} id="css-code" rows={20} cols={70}></Input.TextArea>
              </Col>
              <Col>
                <h2>JS code</h2>
                <hr />
                <br />
                <Input.TextArea placeholder='enter your js code here' onChange={(e) => setJs(e.target.value)} id="js-code" rows={20} cols={70}></Input.TextArea>
              </Col>
            </Row>
            <Button onClick={submitCode}>Run</Button>
            <Row>
              <Col>
                <iframe title={name} style={{width: 1000, height: 300}} id="output"></iframe>
              </Col>
            </Row>
            </div>
            <Button onClick={finishButton} >Submit</Button>
    </Form>
    </NavBar>
  );
};
export default Post;