import React, { useState } from 'react';
import { Steps } from 'antd';
import NavBar from '../Components/NavBar'
import Codemarket from '../Components/Codemarket';
const App = () => {
  const [current, setCurrent] = useState(0);
  const onChange = (value) => {
    setCurrent(value);
  };

  const description = <Codemarket title="CODE MARKET" content="Code Market is a website that is built for selling html, css, js code for people who would want to buy them" />
  const posting = <Codemarket title="WHEN POSTING" content="When posting it is recomended to use a stripe account dedicated to Codemarket and at often times it is not recomended to use your personal stripe account" />
  const features = <Codemarket title="FINNALY" content="More features comming out soon, have fun" />
  return (
    <NavBar>
      <div className='about'>
        <Steps
          current={current}
          onChange={onChange}
          direction="vertical"
          items={[
            {
              title: "Code Market",
              description,
            },
            {
              title: "When Posting",
              description: posting,
            },
            {
              title: "Finnaly",
              description: features,
            },
          ]}
        />
      </div>
    </NavBar>
  );
};
export default App;