import { Form, Input, message, Row, Col, Button } from 'antd'
import axios from 'axios'
import { useState } from 'react'
import NavBar from '../Components/NavBar'
import Card from 'react-bootstrap/Card'

function Delete(){
    const [username, setUsername] = useState('')
    const [password, setPassword] = useState('')
    const [posts, setPosts] = useState([])
    const finishButton = async() => {
        axios.get(`/acc/find/${username}/${password}`).then((res) => {
          if(res.data.length === 0){
            message.error("Error account not found")
          }
          else{
            message.success("Account found")
            axios.get(`/get/code/${username}`).then((res2) => {
                setPosts(res2.data)
            }).catch(error => message.error("Could not be posted "))
          }
          })
      }
      const deletePost = async(id) => {
        try{
            await axios.post("/code/delete", {_id: id})
            message.success("Removed")
        }catch(err){
            message.error("Error")
            console.log(err)
        }
      }
    return(
        <NavBar>
                <Form className="form-delete">
                    <h1>Enter details to delete posts</h1>
                    <hr />
                    <br />
                    <Form.Item label="Username">
                        <Input onChange={(e) => setUsername(e.target.value)} />
                    </Form.Item>
                    <Form.Item label="password">
                        <Input.Password onChange={(e) => setPassword(e.target.value)} />
                    </Form.Item>
                    <Form.Item>
                        <Button onClick={finishButton}>Find</Button>
                    </Form.Item>
                </Form>
                <div >
                    <Row>
                        {posts.map((products) => (
                            <Col xs={24}  sm={6} md={12} lg={6} >
                                    <div className='form-product' >
                                        <Card>
                                        <Card.Img className="card-image" src={products.image} />
                                        <Card.Body>
                                            <Card.Title>{products.name}</Card.Title>
                                            <Card.Text>
                                                {products.description}
                                            </Card.Text>
                                            <p>${products.price}</p>
                                            <Button onClick={() => deletePost(products._id)}>Delete</Button>
                                        </Card.Body>
                                        </Card>
                                    </div>
                            </Col>
                        ))}
                    </Row>
                </div>
        </NavBar>
    )
}

export default Delete