import { useState, useEffect } from 'react';
import { Button, Form, Input, message} from 'antd';
import Axios from 'axios';
import CommentCard from './CardComment'
import { Col } from 'antd'

function Comment({name}){
    const [username, setUsername] = useState('')
    const [userpassword, setPassword] = useState('')
    const [comment, setComment] = useState('')
    const postComment = async() => {
        Axios.get(`/acc/find/${username}/${userpassword}`, {username, userpassword}).then((response) => {
            if(response.data.length === 0){
              message.error("Error")
            }else{
              message.success("Account found")
              Axios.post("/comment/post", {username, password: userpassword,comment: comment, name: name}).then((res) => {
                  message.success("Created")
              }).catch(error => message.error("Error, Username and password could not be found"))
            }
        }).catch(error => message.error("Error"))
    }
    const [Comments, setComments] = useState([])
    useEffect(() => {
      const get = async() => {
          const {data} = await Axios.get(`/comment/find/${name}`)
          setComments(data)
      }
      get()
    }, [])

  const [form] = Form.useForm();
  return (
    <div>
        <h1>Comments</h1>
        <hr />
        <br />
        <Form style={{padding: 20}} form={form} name="control-hooks" onFinish={postComment}>
          <Form.Item name="username" label="username: " rules={[{required: true,},]}>
            <Input onChange={(e) => setUsername(e.target.value)} />
        </Form.Item>
        <Form.Item name="password" label="password:" rules={[{required: true,},]}>
            <Input.Password onChange={(e) => setPassword(e.target.value)} />
        </Form.Item>
        <Form.Item name="comment" label="Comment: " rules={[{required: true,},]}>
            <Input onChange={(e) => setComment(e.target.value)} />
        </Form.Item>
            <Button htmlType="submit">
              Post
            </Button>
        </Form>
        <hr />
    <Col>
      {Comments.map((c) => (
          <Col>
            <CommentCard comment={c} />
          </Col>
      ))}
    </Col>

    </div>
  );
};
export default Comment;